import { useState, useEffect } from 'react';
import './mobile-menu.scss'
import Hamburger from 'hamburger-react'
import { NavLink } from 'react-router-dom';
import Social from '../social/Social';
import { ReactComponent as PhoneIcon } from '../../images/icons/phone.svg';

export const MobileMenu = () => {
    const [isOpen, setOpen] = useState(false);

    useEffect(() => {
        if (isOpen) {
            document.querySelector('body').classList.add('menu-open');
        } else {
            document.querySelector('body').classList.remove('menu-open');
        }
    }, [isOpen])

	return (
		<>
            { isOpen &&
                <div className="overlay">
                    <NavLink className="sub-header__link" to="/">УБОРКА ДЛЯ ВЫСЕЛЕНИЯ</NavLink>
                    <NavLink className="sub-header__link" to="/maintenance-cleaning">ПОДДЕРЖИВАЮЩАЯ УБОРКА</NavLink>
                    <NavLink className="sub-header__link" to="/wallpaper">ПОКЛЕЙКА ОБОЕВ</NavLink>
                    <NavLink className="sub-header__link" to="/mold-removal">УСТРАНЕНИЕ ПЛЕСЕНИ</NavLink>
                    <NavLink className="sub-header__link" to="/wall-repair">РЕМОНТ СТЕН</NavLink>
                    <div className="overlay-divider"></div>
                    <a href="tel:+375(25)672-17-91" className="overlay__phone-link">
                        <div className="overlay__phone-icon">
                            <PhoneIcon fill='#559EC7' width="16" height="25" />
                        </div>
                        +375(25)672-17-91
                    </a>
                    <div className="overlay-divider"></div>
                    <Social color={'#559EC7'}/>
                </div>
            }

            <Hamburger toggled={isOpen} toggle={setOpen} size={27} rounded />
        </>
	)
}
